
.chatFormHead{
    background-color: #001C43;
border-radius: 20px 20px 0px 0px;
text-align: center;
}
.chatFormBody{
    background-image: linear-gradient(to bottom right,#b8cde7 0%,#fff 100%);
    height: 75vh;
    position: relative;
}
.rightSideChat{
    text-align: right;
}
.leftSideChat{
    text-align: left;
}
.chatText{
background-color: #f9fcff24;
color: #000;
padding: 15px;
border-radius: 15px;
background-size: cover;
display: inline-block;
box-shadow: 0 .5rem 1rem rgba(0,0,0,.15) !important;
margin-bottom: 0;
}
.chatTextImage{
 border-radius: 50%;
height: 40px;
width: 40px;
margin: 10px;
border: 3px solid #ffffffa1;
/* background-color: #d20001; */
}
.chatTextTime{
    color: #a9a9a9;
}
.darkOrenge{
    background-color: #d20001 ;
    text-align: center;
}

.btnSend{
right: -8px;
background-color: #001C43;
color: #fff;
border-radius: 5px;
border: 2px solid #fff;
}
.bedge{
    /* width: 200px !important; */
    background-color: black !important;
    bottom: 0px;
    position: absolute;
    border: 1px solid #fff;
    
}