.navBar {
  border: 5px solid #0000000a;
  box-sizing: border-box;
}
.logout {
  padding: 10px;
  background-color: #d20001;
  margin: 0px 16px;
  text-align: center;
  color: #fff;
  border: none;
  border-radius: 5px;
  margin-left: 30px;
}
.logout:hover {
  padding: 10px;
  background-color: #001c43;
  margin: 0px 16px;
  text-align: center;
  color: #fff;
  margin-left: 30px;
}
.socialIconStyle {
  color: #fff;
  background-color: #001c43;
  margin: 5px;
  padding: 10px 12px;
  height: 40px;
  width: 40px;
  border-radius: 50%;
  line-height: 20px;
  text-decoration: none;
  position: relative;
  display: inline-block;
}
.socialIconStyle:hover {
  background: #d20001;
}
.HeaderEnd {
  text-align: right;
}
.userImg {
  object-fit: fill;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 2px solid #fff;
}
.userImg:before {
  display: inline-block;
  content: "\f0d7";
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  background-color: #001c43;
}
